export default {
	data() {
		return {
			TabCur:0,
			tabList:['工作日志','叫快递','我的报修'],
		}
	},
	methods: {
		tabSelect(event){
			this.TabCur = event;
		},
		//新建
		createWork(event){
			if(event==0){
				uni.navigateTo({
					url:'../create-log/create-log'
				})
			}else if(event==1){
				uni.navigateTo({
					url:'../../shop-center/create-order/create-order'
				})
			}
		},
		//查看详情
		itemDetail(event){
			if(event==0){
				uni.navigateTo({
					url:'../log-detail/log-detail'
				})
			}else if(event==1){
				uni.navigateTo({
					url: '../../shop-center/repair-order-item/repair-order-item',
				});
			}
		}
	}
}
